@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.headerCont{
    padding: 1rem 0;
    background-color: rgba(0, 0, 0, 0.582);
    margin-bottom: 1rem;
}
.titHead{
    font-size: 3rem;
    margin: 0;
    text-align: center;
    font-family: 'Alfa Slab One', cursive;
    color: #000000;
    text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
}
.container{
    background-color: #003049;
    min-height: 100vh;
}

.winnerAnnouncement{
    background-color: rgba(0, 0, 0, 0.568);
    min-height: 100vh;
    min-width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    -webkit-justify-content: center;
            justify-content: center;
}

.textWinner{
    margin: 0;
    color: white;
}
.userBoard{
    display: grid;
    grid-template-columns: repeat(10, 5vmin);
    grid-template-rows: repeat(10, 5vmin);
    margin-bottom: 1rem;
}

.agruparBoard{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.textB{
    padding: 0.5rem 1rem;
    background-color: rgba(0, 0, 0, 0.247);
    border-radius: 5px;
    width: inherit;
}

.tB{
    margin: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    color: white;
}

@media (max-width: 800px) {
    .tB{
        font-size: 0.7rem;
    }
}

:root{
    --water-col: #d4f1f9;
    --ship-col: #787f86;
    --hit-cool: #43707e;
    --ship-hit: #8b0000;
    --ship-hov: #4c535a;
    --water-hov: #a4c3cc;
}

.boxinboard{
    border: 1px solid rgba(0, 0, 0, 0.486);
}

.water{
    background-color: #d4f1f9;
    background-color: var(--water-col);
    cursor: pointer;
}
.water:hover{
    background-color: #a4c3cc;
    background-color: var(--water-hov);
}

.ship{
    background-color: #787f86;
    background-color: var(--ship-col);
    cursor: pointer;
}
.ship:hover{
    background-color: #4c535a;
    background-color: var(--ship-hov);
}

.hitN{
    background-color: #43707e;
    background-color: var(--hit-cool);
}

.hitS{
    background-color: #8b0000;
    background-color: var(--ship-hit);
}
.titlePlace{
    background-color: whitesmoke;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 5px;
    padding: 5px;
}

.titPlace{
    margin: 0;
    font-size: 1rem;
}
.boardsCont{
    display: -webkit-flex;
    display: flex;
    grid-gap: 4rem;
    gap: 4rem;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.boardP{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.botonP{
    width: 100px;
    padding: 0.3rem;
    margin: 0.5rem 0.75rem;
    text-transform: capitalize;
}

.botonesT{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.containerWP{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 1rem;
    gap: 1rem;
}
.placed{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}
.containerALL{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.textingGame{
    width: 100%;
    background-color: rgba(0, 0, 0, 0.192);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 5px;
    padding: 1rem 0;
    margin-bottom: 1rem;
}

.textW{
    margin: 0;
    color: white;
    text-shadow: 0 0 2px black;
}

@media (max-width: 900px) {
    .containerWP{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }
}
@media (max-width: 800px) {
    .textW{
        font-size: 1rem;
    }
}
